@use 'cor';

.faq {
    width: 100%;
    position: absolute;
    top: 90vw;

    &__questions__item{
        margin-bottom: 1em;

        &__question {
            background-color: #000;
            padding: 10px;
            cursor:pointer;
            display: flex;
            justify-content: space-between;
    
            &::after{
                content: '+';
                color: cor.$cortext;
                font-size: 2vw;
                font-weight: bold;
                font-family: Papyrus, fantasy;
            }
        }
    
        &__answer {
            background-color: #222121;
            height: 0;
            overflow: hidden;
            padding: 0 10px;
            transition: height, 0.5s ease;
        }
    
        &--is-open{

            .faq__questions__item__question::after {
                content: '-';}

            .faq__questions__item__answer {
                background-color: #222121;
                height: auto;
                padding-top: 10px;
                padding-bottom: 10px;
                transition: height, 0.5s ease;
            }
        }

    }

    @media (max-width:640px){
        width: 100%;
    position: absolute;
    top: 130vh;
    h3{font-size: 20px;}
    p{ font-size: 16px;}
    }
    

    

}
